.menu {
    position: relative;
    left: 20px;
    margin-bottom: 10px;
    font-family: 'Roboto';
}

.menu__item {
    color: #fff;
    text-decoration: none;
    font-size: 24px;
    margin-right: 15px;  
}

.menu__item:last-of-type {
    margin-right: 0;
}

.menu__item_active {
    font-weight: bold;
    font-size: 26px;
}
